                            <el-checkbox v-model="share_weibo"></el-checkbox>
                        </div>
                    </div>
                    <div class="tc" style="margin-top: 20px;">
                        <el-button class="btn-next" type="primary" round @click="submitForm"></el-button>
                    </div>
                </el-form>
