<script>
    import 'quill/dist/quill.core.css';
    import 'quill/dist/quill.snow.css';
    import 'quill/dist/quill.bubble.css';
    import {quillEditor} from 'vue-quill-editor';
    import vHead from "@/components/Header.vue";
    import projectApi from "@/network/api/project";

    export default {
        name: "Step1",
        components: {vHead, quillEditor},
        data() {
            let validateContent = (rule, value, callback) => {
                if (value === "") {
                    callback(new Error(""));
                } else {
                    callback();
                }
            }

            return {
                editorOption: {
                    placeholder: ''
                },
                share_weibo: true,
                form: {
                    title: '',
                    sub_title: '',
                    type_name: '',
                    content: '',
                },
                rules: {
                    title: [{required: true, message: "", trigger: "blur"}],
                    sub_title: [{required: true, message: "", trigger: "blur"}],
                    type_name: [{required: true, message: "", trigger: "change"}],
                    content: [{required: true, message: "", trigger: "blur"}],
                }
            };
        },
        created() {
        },
        computed: {
            editor() {
                return this.$refs.myQuillEditor.quill
            }
        },
        methods: {

            submitForm() {

                this.$refs.form.validate((valid, fields) => {
                    if (valid) {
                        this.$loading.show('');
                        let user = this.$store.getters['user/get'];
                        user.company_id = '1231';//

                        let params = this.form;
                        params.company_id = user.company_id;

                        projectApi
                            .create(params)
                            .then(res => {
                                this.$loading.hide();
                                console.error("===> ", res);
                                this.$message.success("");
                                this.$router.push('/project/add_step2');
                            })
                            .catch(res => {
                                this.$loading.hide();

                                this.$message.error(
                                    res && res.data && res.data.msg ? res.data.msg : ""
                                );
                            });

                        return true;
                    } else {
                        return false;
                    }
                });
            },
        }
    }
</script>

